var backpage,backtype;
export default {
	data() {
		return {
			userInfo:{
				phoneNum:'',
				password:''
			},
			checked:true,
		};
	},
	onLoad(e){
		backpage = e.backpage;
		backtype = e.backtype;
		var userInfo = uni.getStorageSync("USERINFO");
		if(userInfo){
			this.userInfo = JSON.parse(userInfo)
		}
		
	},
	methods:{
		login(){
			uni.showLoading({
				title:"登录中..",
				mask:true
			})
			this.$uniRequest(
				this.$baseUrl+'/v1/login/doLogin',
				{
					mobile:this.userInfo.phoneNum,
					member_pass:this.userInfo.password
				},
				(res)=>{
					if(res.data.code == 200){
						uni.hideLoading();
						uni.showToast({
							title:"登录成功",
							mask:true,
							duration:1500
						})
						uni.setStorage({
							key:"TOKEN",
							data:res.data.data.token
						})
						if(this.checked){
							uni.setStorage({
								key:"USERINFO",
								data:JSON.stringify(this.userInfo)
							})
						}
						setTimeout(()=>{
							if(!backpage){
								uni.switchTab({
									url:'/pages/mine/mine'
								});
							}else{
								if(backtype == 1){
								    uni.redirectTo({url:backpage});
								}else{
								    uni.switchTab({url:backpage});
								}
							}
						},1500)
					}else{
						uni.hideLoading();
						uni.showToast({
							title:res.data.msg,
							icon:"none"
						})
					}
				},
				true
			)
		},
		toRegister(){
			uni.navigateTo({
				url:'/pages/register/register'
			})
		},
		toForget(){
			uni.navigateTo({
				url:'/pages/forget/forget'
			})
		}
	}
}
